import { Component, Inject } from '@angular/core';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { BehaviorSubject } from 'rxjs';
import CodeMirror from 'codemirror';
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'settings',
  templateUrl: 'settings.html'
})
export class SettingsComponent {

  doc: BehaviorSubject<CodeMirror.Doc> = new BehaviorSubject(this.gsp.global_config.userSettings);
  interval: NodeJS.Timer;

  constructor(public gsp: GlobalServiceProvider, @Inject(DOCUMENT) private document: Document) { }

  prepareJSON(object: object, key?: string): string {
    let str: string = '';
    for (let obj in object) {
      if (typeof object[obj] == 'string') {
        str = String().concat(str,
          `
          '${key != undefined ? key + '.' : ''}${obj}':'${object[obj]}',`);
      } else {
        str = String().concat(str, this.prepareJSON(object[obj], `${key != undefined ? key + '.' : ''}${obj}`));
      }
    }
    return str;
  }

  changeSettings(settings: 'project' | 'user') {
    if (settings == 'project') {
      // this.doc.next(this.gsp.global_config.projectSettings);
      console.log(JSON.parse(this.gsp.global_config.defaultSettings.getValue()));
      let json = this.prepareJSON(JSON.parse(this.gsp.global_config.defaultSettings.getValue()));
      this.doc.next(new CodeMirror.Doc(json));
    } else {
      this.doc.next(this.gsp.global_config.userSettings);
    }
  }

  updateTheme() {
    try {
      let settings = JSON.parse(this.doc.getValue().getValue());
      for (const value of Object.keys(settings)) {
        let object = value.split('.', 7);
        if (object[0] == 'styles') {
          if (object[1] == 'theme') {
            this.document.documentElement.style.setProperty(`--${object[2]}-color-${object[3]}`, `${settings[value]}`);
            console.log(`--${object[2]}-color-${object[3]}`, `${settings[value]}`);
          }
        }
      }
    } catch (err) {
      null;
    }
  }

  ngAfterViewInit() {
    
    // this.doc.next(this.gsp.global_config.userSettings);
    // this.interval = setInterval(() => {
    //   this.updateTheme();
    // }, 1000);
  }

  ngOnDestroy() {
    clearInterval(this.interval);
  }
}



