import { menuListInterface, componentDataInterface } from "../utilities/interface";
import { ComponentFactory, ViewContainerRef, ComponentFactoryResolver } from "@angular/core";
import { MtrToggleComponent } from "../components/mtr-toggle/mtr-toggle";
import { MtrButtonComponent } from "../components/mtr-button/mtr-button";
import { MtrRangeComponent } from "../components/mtr-range/mtr-range";

export function addComponentFromDump(_component: menuListInterface, _container: ViewContainerRef, _resolver: ComponentFactoryResolver) {
    let component: ComponentFactory<any>;
    switch (_component.component) {
        case 'MtrToggleComponent':
            component = _resolver.resolveComponentFactory(MtrToggleComponent);
            _container.createComponent(component);
            break;
        case 'MtrButtonComponent':
            component = _resolver.resolveComponentFactory(MtrButtonComponent);
            _container.createComponent(component);
            break;
        case 'MtrRangeComponent':
            component = _resolver.resolveComponentFactory(MtrRangeComponent);
            _container.createComponent(component);
            break;
        default:
            component = _resolver.resolveComponentFactory(MtrToggleComponent);
            _container.createComponent(component);
            break;
    }
}

export function recreateComponenFromDump(_component: any, _container: ViewContainerRef, _resolver: ComponentFactoryResolver) {
    let component: ComponentFactory<any>;
    let data: componentDataInterface = _component.data;
    switch (data.config.component) {
        case 'MtrToggleComponent':
            component = _resolver.resolveComponentFactory(MtrToggleComponent);
            _container.createComponent(component).instance.data = _component.data;
            break;
        case 'MtrButtonComponent':
            component = _resolver.resolveComponentFactory(MtrButtonComponent);
            _container.createComponent(component).instance.data = _component.data;
            break;
        case 'MtrRangeComponent':
            component = _resolver.resolveComponentFactory(MtrRangeComponent);
            _container.createComponent(component).instance.data = _component.data;
            break;
        default:
            component = _resolver.resolveComponentFactory(MtrToggleComponent);
            _container.createComponent(component);
            break;
    }
}

export function addGlobalCSSFromDump(css: string) {
    try {
        let prevStyle = document.getElementById('style-page-designer');
        prevStyle.parentNode.removeChild(prevStyle);
    } catch (err) {
        null;
    }
    let edit = String().concat(`}`, css);
    edit = edit.replace(/}/g, `}.style-page-designer `);
    edit = edit.slice(1, edit.length - 21);
    let style = document.createElement('style');
    style.setAttribute('id', 'style-page-designer');
    style.innerHTML = edit;
    document.body.appendChild(style);
}


