import { Component, NgZone } from '@angular/core';
import { UserPopOverComponent } from '../user-pop-over/user-pop-over';
import { PopoverController, Popover, Events } from 'ionic-angular';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { ElectronServiceProvider } from '../../providers/electron-service/electron-service';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';

@Component({
  selector: 'menu-bar',
  templateUrl: 'menu-bar.html'
})
export class MenuBarComponent {

  isOpen: boolean = false;
  popOver: Popover = this.popoverCtrl.create(UserPopOverComponent, {}, {
    cssClass: 'mtr-user-popover'
  });
  userName: string = '';
  runningIsAllowed: boolean = true;

  constructor(public popoverCtrl: PopoverController, public asp: AuthenticationServiceProvider, private esp: ElectronServiceProvider, public gsp: GlobalServiceProvider, private events: Events, private zone: NgZone) {
    this.popOver.onDidDismiss(() => {
      this.isOpen = false;
    });
    this.asp.user.subscribe((val) => {
      this.zone.run(() => {
        if (val != null) {
          this.userName = val.displayName;
        } else this.userName = '';
      });
    });

    this.events.subscribe('electron-service-dump : menu-bar && console : run-zone', ()=>{
      this.zone.run(()=>{
        console.log('Updating zone');
      });
    });
  }

  clicked(event: string) {
    switch (event) {
      case 'minus':
        this.esp.minimize();
        break;
      case 'maximize':
        this.esp.maximise();
        break;
      case 'close':
        this.esp.closeWindow();
        break;
      case 'run-setup':
        if (this.runningIsAllowed) {
          this.runningIsAllowed = false;
          this.events.publish('component-menu-bar : electron-service : run-setup');
          setTimeout(() => {
            this.runningIsAllowed = true;
          }, 1000);
        }
        break;
      case 'stop':
        this.events.publish('component-menu-bar : electron-service : stop');
        break;
    }
  }

  toggleUserPopOver(event) {
    if (this.isOpen) {
      this.popOver.dismiss();
      this.isOpen = false;
    } else {
      this.popOver.present({
        ev: event,
      });
      this.isOpen = true;
    }
  }
}



