import { Component, Input, Output, EventEmitter } from '@angular/core';
import { AlertController, AlertOptions, NavController } from 'ionic-angular';
import { componentDataInterface, tabDataInterface } from '../../utilities/interface';

@Component({
  selector: 'hover',
  templateUrl: 'hover.html'
})
export class HoverComponent {

  @Input() component;
  @Output() clickEvent: EventEmitter<tabDataInterface> = new EventEmitter();
  data: componentDataInterface;

  constructor(private alertCtrl: AlertController, private navCtrl: NavController) { }

  ngAfterViewInit() {
    this.data = this.component.data;
  }

  code() {
    this.clickEvent.emit({
      component: this.component,
      mode: 'Javascript'
    });
  }

  style() {
    this.clickEvent.emit({
      component: this.component,
      mode: 'CSS'
    });
  }

  edit() {
    let alertOption: AlertOptions = {
      title: 'Configure',
      message: 'If you ever get lost. Refer to the documentation.',
      inputs: [
        {
          type: 'text',
          name: 'variable',
          value: this.data.config.variable,
          placeholder: 'variable',
          label: 'Variable'
        },
      ],
      buttons: [
        {
          text: 'Save',
          handler: (data) => {
            let previousVariable = this.data.config.variable;
            this.data.config.variable = data.variable
            // this.component.data = this.data;
            // this.events.publish('hover : socket-service : update-component', this.component, previousVariable);
          }
        },
        {
          text: 'Cancel',
          role: 'Cancel'
        }
      ]
    }
    this.alertCtrl.create(alertOption).present();
  }
}



