import { Component, Output, EventEmitter, Input } from '@angular/core';
import { ToastController } from 'ionic-angular';
import { tabDataInterface } from '../../utilities/interface';
import { MatSnackBar, MatSnackBarConfig } from '../../../node_modules/@angular/material';

@Component({
  selector: 'codebar',
  templateUrl: 'codebar.html'
})
export class CodebarComponent {

  @Output() code = new EventEmitter();
  @Input() mode: tabDataInterface["mode"];

  constructor(private toastCtrl: ToastController, private snackbar: MatSnackBar) { }

  save_js() {
    this.code.emit('saveJS');
    const toast = this.toastCtrl.create({
      message: 'Saved',
      duration: 1200,
      position: 'bottom',
      cssClass: 'toast'
    });
    toast.present();
  }

  save_css() {
    this.code.emit('saveCSS');
    const toast = this.toastCtrl.create({
      message: 'Saved',
      duration: 1200,
      position: 'bottom',
      cssClass: 'toast'
    });
    toast.present();
  }

  indent() {
    this.code.emit(this.mode);
    // const toast = this.toastCtrl.create({
    //   message: 'Saved',
    //   duration: 1200,
    //   position: 'bottom',
    //   cssClass: 'toast'
    // });
    // toast.present();
    let config: MatSnackBarConfig = {
      duration: 1200,
      horizontalPosition: 'right',
      announcementMessage: ''
    };
    this.snackbar.open('Ok :)', '', config);
  }

  showInterface() {
    this.code.emit('showInterface');
  }
}



