import { ElectronServiceProvider } from "./electron-service";
import { MatSnackBarConfig } from "@angular/material";
import { MyDashboardsComponent } from "../../components/my-dashboards/my-dashboards";
import { NewFileDialogComponent } from "../../components/new-file-dialog/new-file-dialog";
import { openFileFromDump, saveFileFromDump } from "./electron-service-assets-save&&open";
import { packageJSON } from "../../utilities/code-samples";
import { js_beautify } from 'js-beautify';


export function createNewFileFromDump(_this: ElectronServiceProvider) {
    let timestamp = new Date(Date.now());
    let string = `Quark-${timestamp.getDate()}-${timestamp.getMonth()}-${timestamp.getFullYear()}`;
    _this.dialog.showSaveDialog(_this.BrowserWindow.fromWebContents(_this.currentWebContents), {
        title: 'Save Dashboard',
        defaultPath: `${string}.qrk`,
        filters: [{
            extensions: ['qrk'],
            name: 'Quark'
        }]
    }, (filePath) => {
        saveFileFromDump(_this, filePath);
        saveFileFromDump(_this, filePath, 'package.json', js_beautify(JSON.stringify(packageJSON)));
    });
}

export function newWindowHandleFromDump(_this: ElectronServiceProvider) {
    if (`${_this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW.length}` == '0') {
        _this.BrowserWindow.fromWebContents(_this.currentWebContents).show();
        _this.currentWebContents.openDevTools();
    } else {
        let path: string = _this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW;
        _this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW = "";
        openFileFromDump(path, _this, true);
        console.log("Total Browser Windows", _this.BrowserWindow.length);
    }
};

export function showSnackBarFromDump(_this: ElectronServiceProvider, component: 'my-dashboards' | 'new-file-dialog') {
    let config: MatSnackBarConfig = {
        horizontalPosition: 'right',
        panelClass: `mtr-${component}-component-snack-bar`,
        announcementMessage: "Hello"
    }
    if (component == 'my-dashboards') {
        _this.snackBarRef = _this.snackBar.openFromComponent(MyDashboardsComponent, config);
    } else {
        _this.snackBarRef = _this.snackBar.openFromComponent(NewFileDialogComponent, config);
    }
}

export function createNewWindowFromDump(_this: ElectronServiceProvider, filePath: string) {
    _this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW = `${filePath}`;
    let newWindow = new _this.BrowserWindow({
        height: 600,
        width: 800,
        show: false,
        frame: false
    });
    newWindow.loadURL(`http://localhost:8100`);
}

export function showAlertFromDump(_this: any, type: 'info' | 'error' | 'question' | 'warning', title: string, message: string, buttons: Array<string>, resolve) {
    _this.dialog.showMessageBox(_this.BrowserWindow.fromWebContents(_this.currentWebContents), {
        type: type,
        title: title,
        message: message,
        buttons: buttons
    }, (response: number) => {
        resolve(response);
    });
}



