import { Component, ViewChild, ElementRef, NgZone } from '@angular/core';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { consoleDataInterface } from '../../utilities/interface';
import { Events } from 'ionic-angular';
import { Subject } from 'rxjs/Subject';

@Component({
  selector: 'console',
  templateUrl: 'console.html'
})
export class ConsoleComponent {

  data: consoleDataInterface['data'] = [];
  maps = {
    log: {
      color: '#000000',
      icon: 'return-right'
    },
    warn: {
      color: '#ffff9a',
      icon: 'warning'
    },
    error: {
      color: '#f53d3d',
      icon: 'close'
    },
    right: {
      color: '#25d55f',
      icon: 'checkmark'
    },
    indent: {
      color: '#000000',
      icon: 'arrow-dropright'
    }
  }
  flag: 'terminal' | 'console' = 'console';
  resizeEvent: Subject<any> = new Subject();

  constructor(private gsp: GlobalServiceProvider, private events: Events, private zone: NgZone) {
    this.data = this.gsp.consoleData.data;
    this.events.subscribe('electron-service-dump : menu-bar && console : run-zone', () => {
      this.zone.run(() => {
        console.log('Updating zone');
      });
    });
    this.events.subscribe('electron-service : component-console && component-terminal : run-npm-install', () => {
      this.zone.run(() => {
        this.flag = 'terminal';
        this.maximiseConsole(480);
      });
    });
  }

  @ViewChild('myDiv') myDivRef: ElementRef;
  @ViewChild('myDivHeader') myDivHeaderRef: ElementRef;
  @ViewChild('myContent') myContentRef: ElementRef;
  myDiv: HTMLElement;
  myDivHeader: HTMLElement;
  myContent: HTMLElement;

  ngAfterViewInit() {
    this.myDiv = this.myDivRef.nativeElement;
    this.myDivHeader = this.myDivHeaderRef.nativeElement;
    this.myContent = this.myContentRef.nativeElement;
    this.dragster();
    this.myDiv.style.top = this.gsp.consoleData.top + 'px';
    this.myDiv.style.height = -this.gsp.consoleData.top + 'px';
    this.myContent.scrollTop = this.myContent.scrollHeight;
    // this.resizeTerminal();
    this.minimizeConsole();
  }

  showFlag(flag: 'terminal' | 'console') {
    this.flag = flag;
    this.resizeTerminal();
  }

  resizeTerminal() {
    setTimeout(() => {
      this.resizeEvent.next('');
    }, 100);
  }

  minimizeConsole() {
    this.myDiv.style.height = '30px';
    this.myDiv.style.top = '-30px';
    this.gsp.consoleData.top = -30;
    this.events.publish('component-console : page-designer : update-container-height');
  }

  maximiseConsole(height: number) {
    if (height) {
      this.myDiv.style.height = `${height}px`;
      this.myDiv.style.top = `-${height}px`;
      this.gsp.consoleData.top = -height;

    } else {
      this.myDiv.style.height = '200px';
      this.myDiv.style.top = '-200px';
      this.gsp.consoleData.top = -200;
    }
    this.myDiv.scrollTop = this.myDiv.scrollHeight;
    this.myContent.scrollTop = this.myContent.scrollHeight;
    this.resizeTerminal();
    this.events.publish('component-console : page-designer : update-container-height');
  }

  clearConsole() {
    this.gsp.consoleData.data = [];
    this.data = [];
  }

  contextMenu(event) {
    event.preventDefault();
  }

  dragster() {
    let self = this;
    dragElement(this.myDiv);
    function dragElement(elmnt) {
      var pos2 = 0, pos4 = 0;
      self.myDivHeader.onmousedown = dragMouseDown;
      function dragMouseDown(e) {
        e = e || window.event;
        e.preventDefault();
        // get the mouse cursor position at startup:
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag;
      }

      function elementDrag(e) {
        self.events.publish('component-console : page-designer : update-container-height');
        e = e || window.event;
        e.preventDefault();
        // calculate the new cursor position:
        pos2 = pos4 - e.clientY;
        pos4 = e.clientY;
        // set the element's new position:

        if (pos2 > -36) {///Import/ant to prevent flicker
          elmnt.style.height = -(elmnt.offsetTop - pos2) + "px";
        }
        if ((elmnt.offsetTop - pos2) < -30 && (elmnt.offsetTop - pos2) > -500) {
          let pos = (elmnt.offsetTop - pos2);
          self.resizeEvent.next('');
          if (pos > -100) {
            self.minimizeConsole();
          } else {
            self.gsp.consoleData.top = (elmnt.offsetTop - pos2);
            elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
          }
        }
      }

      function closeDragElement() {
        /* stop moving when mouse button is released:*/
        document.onmouseup = null;
        document.onmousemove = null;
        self.events.publish('component-console : page-designer : update-container-height');
      }
    }
  }
}



