import { Component, Input, Output, EventEmitter } from '@angular/core';
import { Subject } from 'rxjs';
import CodeMirror from 'codemirror';
import * as monaco from 'monaco-editor';
import { NgxEditorModel } from 'ngx-monaco-editor';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';

@Component({
  selector: 'monaco-editor',
  templateUrl: 'monaco-editor.html'
})
export class MonacoEditorComponent {

  @Input('document') document: Subject<CodeMirror.Doc>;
  model: NgxEditorModel = null;
  previousDocument: CodeMirror.Doc = null;
  code: string = '';
  mode: string = this.gsp.currentTab.mode;

  editorOptions: monaco.editor.IEditorConstructionOptions = { theme: 'vs-dark', language: 'json' };

  constructor(private gsp: GlobalServiceProvider) { }

  onInit(event) {
    console.log(event);
  }

  ngAfterContentInit() {
    this.document.subscribe((doc) => {
      this.saveDocument();
      this.code = doc.getValue();
      this.previousDocument = doc;
      this.changeTab();
      this.changeTab();
    });
  }

  ngAfterViewInit() {
    this.document.subscribe((doc) => {
      this.saveDocument();
      this.mode = this.gsp.currentTab.mode;
      this.code = doc.getValue();
      this.previousDocument = doc;
      this.changeTab();
    });
  }

  changeTab() {
    if (this.gsp.currentTab.mode == 'Javascript') {
      this.model = {
        value: this.code,
        language: 'javascript'
      }
    } else if (this.gsp.currentTab.mode == 'CSS') {
      this.model = {
        value: this.code,
        language: 'css'
      }
    } else if (this.gsp.currentTab.mode == 'JSON') {
      this.model = {
        value: this.code,
        language: 'json'
      }
    } else if (this.gsp.currentTab.mode == 'Interface') {
      this.model = {
        value: this.code,
        language: 'javascript'
      }
    }
  }

  saveDocument() {
    this.previousDocument ? this.previousDocument.setValue(this.code) : null;
  }
}



