import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ElectronServiceProvider } from '../providers/electron-service/electron-service';
import { GlobalServiceProvider } from '../providers/global-service/global-service';
import Mousetrap from 'mousetrap';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage: any = 'MenuPage';

  constructor(private platform: Platform, statusBar: StatusBar,
    splashScreen: SplashScreen, private gsp: GlobalServiceProvider, private esp: ElectronServiceProvider) {
    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
    });
    // this.platform.is('mobileweb') ? null : this.ssp.connectToSocketServer('http://192.168.1.7');
    this.registerAccelerators();
  }

  registerAccelerators() {
    Mousetrap.bind(['command+n', 'ctrl+n'], () => {
      this.esp.showNewFileDialog();
    });
    Mousetrap.bind(['command+s', 'ctrl+s'], () => {
      this.gsp.electronConfig.parsedPath == null ?
        this.esp.showSaveDialog() : this.esp.saveFile();
    });
    Mousetrap.bind(['command+o', 'ctrl+o'], () => {
      this.esp.openInNewWindow();
    });
  }

  clickedOnMenuBar(event: string) {
    switch (event) {
      case 'minus':
        this.esp.minimize();
        break;
      case 'maximize':
        this.esp.maximise();
        break;
      case 'close':
        this.esp.closeWindow();
        break;
    }
  }
}




