import { Component, ViewChild, ElementRef, AfterViewInit, Input, Injector } from '@angular/core';
import { Platform, Events } from 'ionic-angular';
import { Terminal, ITheme } from "xterm";
import { Subject } from 'rxjs/Subject';
import { ElectronServiceProvider } from '../../providers/electron-service/electron-service';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { IPty } from "node-pty";

@Component({
  selector: 'terminal',
  templateUrl: 'terminal.html'
})
export class TerminalComponent implements AfterViewInit {

  @ViewChild('xterm') xtermElement: ElementRef;
  @ViewChild('xtermContainer') xtermContainerElement: ElementRef;
  @Input('resize') resize: Subject<null> = new Subject();
  xterm: Terminal;
  fit: any;
  theme: ITheme;
  ptyProcess: IPty;
  esp: ElectronServiceProvider;
  fontSize: number = 12;
  isFirstWrite: boolean = true;
  npmString: string = '';

  constructor(private platform: Platform, private injector: Injector, private gsp: GlobalServiceProvider, private events: Events) {
    this.esp = this.injector.get(ElectronServiceProvider);
    this.events.subscribe('electron-service : component-console && component-terminal : run-npm-install', (npmString: string) => {
      this.npmString = npmString;
      this.changeDirectory();
    });
  }

  testResize(e) {
    console.log(e);
  }

  changeDirectory() {
    this.xterm.dispose();
    this.ptyProcess.kill();
    this.injectTerminal();
  }

  ngAfterViewInit() {
    this.injectTerminal();
    this.resize.subscribe(() => {
      this.resizeEvent();
    });
    this.resizeEvent();
  }

  ngOnDestroy(): void {
    this.xterm.dispose();
    this.ptyProcess.kill();
  }

  resizeEvent() {
    if (this.platform.is('electron')) {
      let num = Math.floor(this.xtermContainerElement.nativeElement.clientHeight / (this.fontSize + 2)) * (this.fontSize + 2);
      this.xtermElement.nativeElement.style.height = `${num}px`;
      this.fit(this.xterm);
    }
  }

  injectTerminal() {
    let self = this;
    if (this.platform.is('electron')) {
      this.isFirstWrite = true;
      const os = require('os');
      const pty = require('node-pty');
      const shell = process.env[os.platform() === 'win32' ? 'cmd.exe' : 'SHELL'];
      this.ptyProcess = pty.spawn(shell, [], {
        name: 'xterm-color',
        cols: 120,
        rows: 24,
        cwd: this.gsp.electronConfig.parsedPath ? this.gsp.electronConfig.parsedPath.dir : this.esp.mainProcess.cwd(),
        env: this.esp.mainProcess.env
      });
      const Terminal = require('xterm').Terminal;
      this.fit = require('xterm/lib/addons/fit/fit').fit;

      this.xterm = new Terminal();
      this.xterm.open(this.xtermElement.nativeElement);
      this.xtermOptions();
      this.fit(this.xterm);
      this.xterm.on('data', (data) => {
        self.ptyProcess.write(data);
      });
      this.ptyProcess.on('data', function (data) {
        self.xterm.write(data);
        if (self.isFirstWrite) {
          self.isFirstWrite = false;
          self.ptyProcess.write(self.npmString);
        }
      });
    }
  }

  xtermOptions() {
    this.xterm.setOption('fontSize', this.fontSize);
    this.theme = {
      background: '#488aff',
      foreground: '#9381ff',
      cursor: '#9381ff'
    }
  }
}



