import { Component } from '@angular/core';
import { Events } from 'ionic-angular';
import { componentDataInterface, globalConfigInterface, tabDataInterface } from '../../utilities/interface';
import { code_toggle } from '../../utilities/code-samples';
import CodeMirror from 'codemirror';
import { css_beautify } from "js-beautify";
import { GlobalServiceProvider } from '../../providers/global-service/global-service';

@Component({
  selector: 'mtr-toggle',
  templateUrl: 'mtr-toggle.html'
})

export class MtrToggleComponent {
  id: number = Date.now();
  component = this;
  data: componentDataInterface = {
    config: {
      id: this.id,
      component: 'MtrToggleComponent',
      css_class: `mtr-toggle-${this.id}`,
      function: `Mtr_Toggle_${this.id}`,
      variable: `mtr_toggle_${this.id}`
    },
    code: {
      js: new CodeMirror.Doc('', 'javascript'),
      css: new CodeMirror.Doc(css_beautify(code_toggle.css, { indent_size: 2 }), 'css'),
    }
  }

  constructor(private events: Events, private gsp: GlobalServiceProvider) { }

  ngAfterViewInit() {
    this.initialize();
    this.events.publish('misc-components : global-service : add-component', this);
    this.events.subscribe('socket-service : misc-components : update-renderer', (closure: Object) => {
    });
    this.events.subscribe('page-designer : misc-components : initialize', () => {
      this.onInitialize();
    });
  }

  ngOnDestroy() {
    this.events.unsubscribe('socket-service : misc-components : update-renderer');
    this.events.unsubscribe('page-designer : misc-components : initialize')
  }

  change(event) {
    let state = event.value;
    this.events.publish('misc-components : socket-service : firmata', this.data.config.function, [state]);
  }

  onInitialize() {
    let config: globalConfigInterface["js"] = JSON.parse(this.gsp.global_config.json.getValue());
    let my_rendered_view = config.rendered_views.filter((val) => {
      return val.id == this.data.config.id;
    });
  }

  initialize() {
    this.gsp.addCSS(this.data);
  }

  openTab(tab: tabDataInterface) {
    this.events.publish('misc-components : global-service : add-tab', tab);
  }
}



