import { NgModule } from '@angular/core';
import { MtrToggleComponent } from './mtr-toggle/mtr-toggle';
import { IonicModule } from 'ionic-angular';
import { HoverComponent } from './hover/hover';
import { CodebarComponent } from './codebar/codebar';
import { StatusBarComponent } from './status-bar/status-bar';
import { TabsBarComponent } from './tabs-bar/tabs-bar';
import { ActivityBarComponent } from './activity-bar/activity-bar';
import { WidgetsComponent } from './widgets/widgets';
import { MtrButtonComponent } from './mtr-button/mtr-button';
import { MtrRangeComponent } from './mtr-range/mtr-range';
import { CodeEditorComponent } from './code-editor/code-editor';
import { CssInspectorComponent } from './css-inspector/css-inspector';
import { ConsoleComponent } from './console/console';
import { MenuBarComponent } from './menu-bar/menu-bar';
import { MatTooltipModule, MatMenuModule, MatSnackBarModule, MatTabsModule, MatFormFieldModule, MatInputModule, MatProgressBarModule, MatButtonModule, MatDividerModule, MatProgressSpinnerModule, MatStepperModule, MatDialogModule, MatCheckboxModule } from '@angular/material';
import { UserPopOverComponent } from './user-pop-over/user-pop-over';
import { MyDashboardsComponent } from './my-dashboards/my-dashboards';
import { NewFileDialogComponent } from './new-file-dialog/new-file-dialog';
import { TerminalComponent } from './terminal/terminal';
import { TagInputModule } from 'ngx-chips';
import { CodeMirrorPage } from '../pages/code-mirror/code-mirror';
import { SettingsComponent } from './settings/settings';
import { MonacoEditorComponent } from './monaco-editor/monaco-editor';
import { MonacoEditorModule } from 'ngx-monaco-editor';

TagInputModule.withDefaults({
    tagInput: {
        placeholder: 'Add a new tag',
        dragZone: 'zone1',
        theme: 'dark',
        addOnBlur: true,
        separatorKeyCodes: [32, 188],
        modelAsStrings: true
    }
});

@NgModule({
    declarations: [
        MtrToggleComponent,
        HoverComponent,
        CodebarComponent,
        StatusBarComponent,
        TabsBarComponent,
        ActivityBarComponent,
        WidgetsComponent,
        MtrButtonComponent,
        MtrRangeComponent,
        CodeEditorComponent,
        CssInspectorComponent,
        ConsoleComponent,
        MenuBarComponent,
        UserPopOverComponent,
        MyDashboardsComponent,
        NewFileDialogComponent,
        TerminalComponent,
        CodeMirrorPage,
        SettingsComponent,
        MonacoEditorComponent
    ],
    imports: [IonicModule,
        MatTooltipModule,
        MatMenuModule,
        MatTabsModule,
        MatSnackBarModule,
        MatFormFieldModule,
        MatInputModule,
        MatProgressBarModule,
        MatButtonModule,
        MatDividerModule,
        MatProgressSpinnerModule,
        MatStepperModule,
        MatDialogModule,
        MatCheckboxModule,
        TagInputModule,
        MonacoEditorModule.forRoot() 
    ],
    exports: [
        MtrToggleComponent,
        HoverComponent,
        CodebarComponent,
        StatusBarComponent,
        TabsBarComponent,
        ActivityBarComponent,
        WidgetsComponent,
        MtrButtonComponent,
        MtrRangeComponent,
        CodeEditorComponent,
        CssInspectorComponent,
        ConsoleComponent,
        MenuBarComponent,
        UserPopOverComponent,
        MyDashboardsComponent,
        NewFileDialogComponent,
        TerminalComponent,
        CodeMirrorPage,
        SettingsComponent,
        MonacoEditorComponent
    ],
    entryComponents: [MtrToggleComponent, MtrButtonComponent, MtrRangeComponent, CodeEditorComponent, UserPopOverComponent, MyDashboardsComponent, NewFileDialogComponent, TerminalComponent]
})
export class ComponentsModule { }



