import { ElectronServiceProvider } from "./electron-service";
import { logger } from "../global-service/global-service-dump";
import Path from 'path';

export function showSaveDialogFromDump(_this: ElectronServiceProvider) {
    let timestamp = new Date(Date.now());
    let string = `Quark-${timestamp.getDate()}-${timestamp.getMonth()}-${timestamp.getFullYear()}`;
    _this.dialog.showSaveDialog(_this.BrowserWindow.fromWebContents(_this.currentWebContents), {
        title: 'Save Dashboard',
        defaultPath: `${string}.qrk`,
        filters: [{
            extensions: ['qrk'],
            name: 'Quark'
        }]
    }, (filePath) => {
        saveFileFromDump(_this, filePath);
    });
}


export function saveFileFromDump(_this: ElectronServiceProvider, filePath: string, fileName?: string, fileData?: string) {
    let self = _this;
    makeDirectory();
    function makeDirectory() {
        if (filePath) {
            logger.right(`Saving file at : ${filePath}`, 40);
            let parse = self.Path.parse(filePath);
            let exists = self.fs.existsSync(`${parse.dir}\\${parse.name}`);
            console.log('Exists', exists);
            if (exists) {
                makeFile();
            } else {
                self.fs.mkdir(`${parse.dir}\\${parse.name}`, (err) => {
                    makeDirectoryErrorHandle(err);
                });
            }
        } else {
            logger.warn('Operation canceled by user', 40);
        }
    }

    function makeDirectoryErrorHandle(err: NodeJS.ErrnoException) {
        if (err) {
            self.showAlert('error', `${err.name}`, `${err.message}`, ['Close']);
            return;
        } else {
            makeFile();
        }
    }

    function makeFile() {
        let parse = self.Path.parse(filePath);
        if (fileName) {
            self.fs.writeFile(`${parse.dir}\\${parse.name}\\${fileName}`, fileData, (err: NodeJS.ErrnoException) => {
                filwWriteErrorHandle(err, parse);
            });
        } else {
            self.fs.writeFile(`${parse.dir}\\${parse.name}\\${parse.name}.qrk`, JSON.stringify(self.gsp.getQuarkFile()), (err: NodeJS.ErrnoException) => {
                filwWriteErrorHandle(err, parse);
            });
        }
    }

    function filwWriteErrorHandle(err: NodeJS.ErrnoException, parse: Path.ParsedPath) {
        if (err) {
            self.showAlert('error', `Error : ${err.name}`, `${err.message}`, ['Close']);
            return;
        } else {
            if (!fileName) {
                self.gsp.electronConfig.parsedPath = self.Path.parse(`${parse.dir}\\${parse.name}\\${parse.name}.qrk`);
                _this.events.publish('electron-service-dump : menu-bar && console : run-zone');
                setRecentDocumentsStorageFromDump(self, `${parse.dir}\\${parse.name}\\${parse.name}.qrk`);
                logger.right(`File saved at : ${self.gsp.electronConfig.parsedPath.dir}`, 40);
            } else {
                if (fileName == 'package.json') {
                    self.runNpmInstall();
                }
            }
        }
    }
}


export function showOpenDialogFromDump(_this: ElectronServiceProvider) {
    _this.dialog.showOpenDialog(_this.BrowserWindow.fromWebContents(_this.currentWebContents), {
        filters: [{
            extensions: ['qrk'],
            name: 'Quark'
        }],
        properties: ['openFile']
    }, (filePath) => {
        if (filePath) {
            openFileFromDump(filePath[0], _this);
        }
    });
}


export function openFileFromDump(filePath: string, _this: ElectronServiceProvider, isOpeningNewWindow?: boolean) {
    let self = _this;
    readFile(filePath);
    function readFile(filePath: string) {
        if (filePath) {
            self.fs.readFile(filePath, 'utf8', (err, data) => {
                callbackHandle(err, data);
            });
        }
    }

    function callbackHandle(err: NodeJS.ErrnoException, data: any) {
        if (err) {
            deleteRecentDocumentsStorageFromDump(self, filePath);
            if (isOpeningNewWindow) {
                self.showAlert('error', err.name, err.message, ['Close'])
                    .then(() => {
                        self.BrowserWindow.fromWebContents(self.currentWebContents).close();
                    })
                    .catch(() => {
                        self.BrowserWindow.fromWebContents(self.currentWebContents).close();
                    })
            } else {
                self.showAlert('error', err.name, err.message, ['Close']);
            }
        } else {
            self.gsp.parseQuarkFile(data)
                .then(() => {
                    self.BrowserWindow.fromWebContents(_this.currentWebContents).show();
                    _this.currentWebContents.openDevTools();
                    _this.app.setBadgeCount(_this.BrowserWindow.getAllWindows().length);
                    let parse = _this.Path.parse(filePath);
                    self.gsp.electronConfig.parsedPath = self.Path.parse(`${parse.dir}\\${parse.name}.qrk`);
                    setRecentDocumentsStorageFromDump(self, `${parse.dir}\\${parse.name}.qrk`);
                    _this.events.publish('electron-service-dump : menu-bar && console : run-zone');
                })
                .catch(() => {
                    if (isOpeningNewWindow) {
                        self.showAlert('error', err.name, err.message, ['Close'])
                            .then(() => {
                                self.BrowserWindow.fromWebContents(self.currentWebContents).close();
                            })
                            .catch(() => {
                                self.BrowserWindow.fromWebContents(self.currentWebContents).close();
                            })
                    } else {
                        logger.error('Failed parsing file. The file may be corrupt!', null, true);
                        self.showAlert('error', 'Operation Failed', 'Failed to parse file', ['Close'])
                    }
                })
        }
    }
}


function setRecentDocumentsStorageFromDump(_this: ElectronServiceProvider, filePath: string) {
    let recentDocuments = window.localStorage.getItem('recentDocuments');
    if (recentDocuments) {
        let parse: Array<string> = JSON.parse(recentDocuments);
        let filter = parse.filter((val) => {
            if (val == filePath) {
                return val;
            }
        });
        if (filter.length == 0) {
            parse.push(filePath);
            window.localStorage.setItem('recentDocuments', JSON.stringify(parse));
        }
        _this.openRecentDocumentsArray = [];
        _this.openRecentDocumentsArray = parse.map((val) => {
            let parse = _this.Path.parse(val);
            return { name: parse.name, path: val }
        });
        console.log('Recent docs exist', parse);
    } else {
        window.localStorage.setItem('recentDocuments', JSON.stringify([filePath]));
        console.log('Recent docs do not exist', [filePath]);
    }
}

function deleteRecentDocumentsStorageFromDump(_this: ElectronServiceProvider, filePath: string) {
    let filterIndex: number = null;
    let docs: Array<string> = JSON.parse(window.localStorage.getItem('recentDocuments'));
    docs.filter((val, index) => {
        if (val == filePath) {
            filterIndex = index;
        }
    });
    docs.splice(filterIndex, 1);
    window.localStorage.setItem('recentDocuments', JSON.stringify(docs));
    _this.openRecentDocumentsArray = [];
    _this.openRecentDocumentsArray = docs.map((val) => {
        let parse = _this.Path.parse(val);
        return { name: parse.name, path: val }
    });
    console.log(_this.openRecentDocumentsArray);
}


