import { Component, Input, Output, EventEmitter } from '@angular/core';
import { menuListInterface } from '../../utilities/interface';

@Component({
  selector: 'widgets',
  templateUrl: 'widgets.html'
})
export class WidgetsComponent {

  @Input() componentsList: Array<menuListInterface>;
  @Output() selectedComponent: EventEmitter<any> = new EventEmitter();

  constructor() { }
  clicked(component: menuListInterface) {
    this.selectedComponent.emit(component)
  }
}



