import { Component, ChangeDetectorRef, NgZone, OnChanges, AfterContentInit, AfterViewInit, OnDestroy } from '@angular/core';
import { componentDataInterface, childProcessDataInterface } from '../../utilities/interface';
import CodeMirror from 'codemirror';
import { css_beautify } from 'js-beautify';
import { code_button } from '../../utilities/code-samples';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { Events } from 'ionic-angular';

@Component({
  selector: 'mtr-button',
  templateUrl: 'mtr-button.html'
})
export class MtrButtonComponent implements AfterContentInit, AfterViewInit, OnDestroy {

  id: number = Date.now();
  component = this;
  data: componentDataInterface = {
    config: {
      id: this.id,
      component: 'MtrButtonComponent',
      css_class: `mtr-button-${this.id}`,
      function: `Mtr_Button_${this.id}`,
      variable: `mtr_button_${this.id}`
    },
    code: {
      js: new CodeMirror.Doc('', 'javascript'),
      css: new CodeMirror.Doc(css_beautify(code_button.css, { indent_size: 2 }), 'css'),
    },
    localData: {}
  }

  constructor(private events: Events, public gsp: GlobalServiceProvider, private zone: NgZone) { }

  change() {
    let data: childProcessDataInterface = {
      functionName: this.data.config.function,
      args: this.data.localData
    }
    this.events.publish('misc-components : electron-service : child-process', data);
  }

  ngAfterContentInit() {
    let data = {
      content: this.data.config.variable,
      color: '#000000',
    }
    this.data.localData = data;
  }

  ngAfterViewInit() {
    this.gsp.addCSS(this.data);
    this.events.publish('misc-components : global-service : add-component', this);
    this.events.subscribe('electron-service-dump : misc-components : update-renderer', (data: object) => {
      this.zone.run(() => {//ng-zone works
        this.data.localData = data[this.data.config.variable];
      });
    });
  }

  ngOnDestroy(): void {
    this.events.unsubscribe('electron-service : misc-components : update-renderer');
  }

  openTab(tab: any) {
    this.events.publish('misc-components : global-service : add-tab', tab);
  }
}



