import { Component, ComponentFactoryResolver, ViewChild, ViewContainerRef, Input, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, Events } from 'ionic-angular';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { Subject } from 'rxjs/Subject';
import { tabDataInterface, componentDataInterface, menuListInterface } from '../../utilities/interface';
import { MatSnackBar, MatSnackBarConfig } from "@angular/material";
import { MTRButtonComponentInterfaceSample, GlobalConfigInterfaceSample } from '../../utilities/code-samples';
import CodeMirror from 'codemirror';
import { js_beautify } from "js-beautify";
import { CodeEditorComponent } from '../../components/code-editor/code-editor';
import { recreateComponenFromDump, addComponentFromDump, addGlobalCSSFromDump } from '../render-components-dump';
import { logger } from '../../providers/global-service/global-service-dump';


@Component({
  selector: 'page-code-mirror',
  templateUrl: 'code-mirror.html'
})

export class CodeMirrorPage {

  document: Subject<CodeMirror.Doc> = new Subject();
  @Input('tab') tab: Subject<tabDataInterface> = new Subject();
  @ViewChild('container', { read: ViewContainerRef }) container: ViewContainerRef;
  @ViewChild('fakeContainer', { read: ViewContainerRef }) fakeContainer: ViewContainerRef;
  styleInterval: NodeJS.Timer;
  mouseMoveEvent: Subject<any> = new Subject();
  mouseLeaveEvent: Subject<any> = new Subject();
  stayFixed: Subject<boolean> = new Subject();
  currentComponentClassName: Subject<string> = new Subject();
  interface: string = '';
  isSnackbarOpen: boolean = false;

  constructor(public navCtrl: NavController, public snackBar: MatSnackBar,
    public gsp: GlobalServiceProvider, private events: Events, public resolver: ComponentFactoryResolver) {
    this.events.subscribe('page-menu : page-designer && page-code-mirror : add-component', (component: menuListInterface) => {
      addComponentFromDump(component, this.fakeContainer, this.resolver);
      logger.log(component.component ? `Adding component ${component.component}` : `Adding class ${component.class}`);
    });
    this.events.subscribe('page-designer : page-code-mirror : open-tab', () => {
      this.updateDocuments(this.gsp.currentTab);
      setTimeout(() => {
        this.updateDocuments(this.gsp.currentTab);
      }, 100);
    });
  }

  ngAfterViewInit() {
    this.updateDocuments(this.gsp.currentTab);
    this.tab.subscribe((tab: tabDataInterface) => {
      this.closeTab(tab);
    });
    setTimeout(() => {
      this.updateDocuments(this.gsp.currentTab);
    }, 100);
  }

  ngOnDestroy(): void {
    clearInterval(this.styleInterval);
    this.snackBar.dismiss();
    this.events.unsubscribe('page-menu : page-code-mirror : add-component');
  }

  showSnackBar() {
    if (this.isSnackbarOpen) {
      this.snackBar.dismiss();
      this.isSnackbarOpen = false;
    } else {
      let config: MatSnackBarConfig = {
        data: { inputDoc: new CodeMirror.Doc(this.interface, "text/x-java"), mode: 'Interface' },
        horizontalPosition: 'right',
        panelClass: 'mtr-code-editor-component-snack-bar',
        announcementMessage: "Hello"
      }
      let ref = this.snackBar.openFromComponent(CodeEditorComponent, config);
      let obs = ref.afterOpened();
      obs.subscribe((val) => {
        this.isSnackbarOpen = true;
      });
    }
  }

  lockContent(event) {
    event.preventDefault();
    this.stayFixed.next(true);
    let config: MatSnackBarConfig = {
      duration: 1000,
      horizontalPosition: 'right'
    }
    this.snackBar.open('Content Locked', '', config);
  }

  preventRightClick(event) {
    event.preventDefault();
  }

  closeTab(tab: tabDataInterface) {
    this.gsp.tabsArray.forEach((val, index, array) => {
      if (val == tab) {
        this.gsp.tabsArray.splice(index, 1);
        if (array.length > 0) {
          if (array.length == index) {
            this.gsp.currentTab = array[index - 1];
          } else {
            this.gsp.currentTab = array[index];
          }
        }
        this.updateDocuments(this.gsp.currentTab);
        //Bug
        setTimeout(() => {
          this.updateDocuments(this.gsp.currentTab);
        }, 50);
      }
    });
  }

  mouseMove(event: MouseEvent) {
    this.mouseMoveEvent.next(event);
  }

  mouseLeave() {
    this.mouseLeaveEvent.next('');
  }

  updateDocuments(currentTab: tabDataInterface) {
    clearInterval(this.styleInterval);
    if (currentTab.other == undefined) {
      this.gsp.designerComponents.map((val: any) => {
        if (val.data.config.id == currentTab.component.data.config.id) {
          if (currentTab.mode != "CSS") {
            this.document.next(this.gsp.currentTab.component.data.code.js);
            this.addInterface(this.gsp.currentTab.component.data.config.component);
          } else {
            this.document.next(this.gsp.currentTab.component.data.code.css);
            this.currentComponentClassName.next(this.gsp.currentTab.component.data.config.css_class);
            this.container.clear();
            recreateComponenFromDump(this.gsp.currentTab.component, this.container, this.resolver);
            this.styleInterval = setInterval(() => {
              this.gsp.addCSS(this.gsp.currentTab.component.data);
            }, 1000);
            addGlobalCSSFromDump(this.gsp.global_config.css.getValue());
          }
        }
      });
    } else if (currentTab.other == "globalConfigJSON") {
      this.document.next(this.gsp.global_config.json);
      this.interface = js_beautify(GlobalConfigInterfaceSample);
    } else if (currentTab.other == "globalConfigCSS") {
      this.document.next(this.gsp.global_config.css);
    } else if (currentTab.other == "globalConfigSetup") {
      this.document.next(this.gsp.global_config.code);
    } else if (currentTab.other == "globalConfigDefaultSettings") {
      this.document.next(this.gsp.global_config.defaultSettings);
    }
  }

  addInterface(component: componentDataInterface['config']['component']) {
    console.log(component);
    switch (component) {
      case 'MtrButtonComponent':
        this.interface = js_beautify(MTRButtonComponentInterfaceSample);
        break;
      default: this.interface = js_beautify(MTRButtonComponentInterfaceSample);
        break;
    }
  }
}



