import { Component, ChangeDetectorRef } from '@angular/core';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { Events } from 'ionic-angular';

@Component({
  selector: 'my-dashboards',
  templateUrl: 'my-dashboards.html'
})
export class MyDashboardsComponent {

  dashboardsList: Array<any> = [];
  showLoading: boolean = true;

  constructor(private asp: AuthenticationServiceProvider, private changeDetector : ChangeDetectorRef, private events : Events) {
    this.dashboardsList = this.asp.userDashboards.getValue();
    this.asp.userDashboards.subscribe((dashboards: Array<any>) => {
      this.dashboardsList = dashboards;
      if (this.dashboardsList != null) {
        this.showLoading = false;
      }
      console.log(this.showLoading);
      this.changeDetector.markForCheck();
    });
    if (this.dashboardsList == null) {
      this.asp.getDashboards();
    } else {
      this.showLoading = false;
    }
  }

  closeSnackBar(){
    this.events.publish('component-new-file-dialog && component-my-dashboards : electron-service : close-snackbar');
  }
}



