import { Component } from '@angular/core';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { Validators, FormControl } from '@angular/forms';
import { Platform } from 'ionic-angular';
import { ElectronServiceProvider } from '../../providers/electron-service/electron-service';

@Component({
  selector: 'user-pop-over',
  templateUrl: 'user-pop-over.html'
})
export class UserPopOverComponent {

  userData: firebase.User;
  hide = true;
  showProgressBar: boolean = false;
  email: FormControl = new FormControl('', [Validators.required, Validators.email]);
  password: FormControl = new FormControl('', [Validators.required, Validators.minLength(8)]);

  constructor(public asp: AuthenticationServiceProvider, private platform: Platform, private esp: ElectronServiceProvider) {
    this.userData = this.asp.user.getValue();
    this.asp.user.subscribe((user) => {
      this.userData = user;
    });
  }

  logForm() {
    this.showProgressBar = true;
    this.asp.loginWithEmailAndPassword(this.email.value, this.password.value)
      .then(() => {
        this.showProgressBar = false;
      })
      .catch(() => {
        this.showProgressBar = false;
      })
  }

  registerUser() {
    if (this.platform.is('electron')) {
      this.esp.openExternalLink('https://link.diymechatronics.com');
    } else {
      window.open('https://link.diymechatronics.com');
    }
  }

  getErrorMessage() {
    return this.email.hasError('required') ? 'You must enter a value' :
      this.email.hasError('email') ? 'Not a valid email' :
        '';
  }

  getErrorMessagePassword() {
    return this.email.hasError('required') ? 'You must enter a value' :
      this.email.hasError('minlength') ? 'Minimum length must be 8 characters' :
        '';
  }
}



