import { Injectable, Injector } from '@angular/core';
import firebase from "firebase/app";
import 'firebase/auth'
import 'firebase/firestore';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { AlertController } from 'ionic-angular';
import { GlobalServiceProvider } from '../global-service/global-service';
import { MatSnackBar } from '@angular/material';
import { ElectronServiceProvider } from '../electron-service/electron-service';

@Injectable()
export class AuthenticationServiceProvider {

  auth = firebase.auth;
  firestore = firebase.firestore;
  actionCode: firebase.auth.ActionCodeSettings;
  isLoggedIn: boolean = false;
  user: BehaviorSubject<firebase.User> = new BehaviorSubject(null);
  userDashboards: BehaviorSubject<Array<firebase.firestore.DocumentData>> = new BehaviorSubject(null);
  esp : any;


  constructor(private alertCtrl: AlertController, private gsp: GlobalServiceProvider,private injector: Injector, private snackbar: MatSnackBar) {
    this.esp = this.injector.get(ElectronServiceProvider);   
    this.auth().onAuthStateChanged((user) => {
      this.user.next(user);
      console.log(user);
    });
    this.firestore().settings({
      timestampsInSnapshots: true
    });
  }

  loginWithEmailAndPassword(email: string, password: string): Promise<void> {
    /**
     * Log user with email and password. Returns a promise.
     * Initiated from : user-pop-over component.
     */
    let self = this;
    return new Promise((resolve, reject) => {
      this.auth().signInWithEmailAndPassword(email, password)
        .then((result) => {
          console.log(result, 'User successfully logged');
          this.user.next(result.user);
          resolve();
        })
        .catch((err) => {
          console.log(err, "Could not log in user");
          self.alertCtrl.create({
            title: "Error : Could not log in",
            subTitle: err.code,
            message: err.message,
            buttons: [{
              text: "Close",
              role: "close"
            }]
          }).present();
          reject();
        })
    })
  }

  logoutUser() {
    this.auth().signOut();
  }

  getDashboards() {
    this.firestore().collection('Users').doc(this.user.getValue().email).collection('Public').get()
      .then((snap: firebase.firestore.QuerySnapshot) => {
        let array: Array<firebase.firestore.DocumentData> = [];
        snap.docs.forEach((doc: firebase.firestore.QueryDocumentSnapshot) => {
          array.push(doc);
        });
        this.userDashboards.next(array);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  uploadDashboard() {
    /**
     * Uploads the current file to cloud.
     * Initiated from : Activity-bar-component.
     * 1. First check if the file is saved.
     * 2. If file is not saved then show alert to save file.
     * 3. If file is saved then upload to cloud. 
     */
    if (this.gsp.electronConfig.parsedPath) {
      this.gsp.showLoading = true;
      this.firestore().collection('Users').doc(this.user.getValue().email).collection('Public').doc(this.gsp.electronConfig.parsedPath.name)
        .set(this.gsp.getQuarkFile())
        .then(() => {
          this.snackbar.open('File upload complete!', 'Close', {
            duration: 1800,
            horizontalPosition: 'right'
          });
          this.gsp.showLoading = false;
        })
        .catch((err) => {
          this.gsp.showLoading = false;
        })
    } else {
      this.esp.showAlert('info', 'Oops..!', 'You must save the file first.', ['Save', 'Cancel'])
        .then((response: number) => {
          console.log(response);
          if (response == 0) {
            this.esp.showSaveDialog();
          }
        })
        .catch((err) => {
          console.log('Not electron. Sorry not sorry!', err);
        })
    }
  }
}



