import { Component, Input, Output, EventEmitter } from '@angular/core';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { tabDataInterface } from '../../utilities/interface';

@Component({
  selector: 'tabs-bar',
  templateUrl: 'tabs-bar.html'
})
export class TabsBarComponent {

  constructor(public gsp: GlobalServiceProvider) { }

  @Input() tabsArray: Array<tabDataInterface> = [];
  @Output() closeTab: EventEmitter<tabDataInterface> = new EventEmitter();
  @Output() openTab: EventEmitter<tabDataInterface> = new EventEmitter();
  @Input() selectedTab: tabDataInterface;

  clicked(tab: tabDataInterface) {
    this.openTab.emit(tab);
  }

  CloseTab(tab: tabDataInterface) {
    this.closeTab.emit(tab);
  }
}



