import { Component, ElementRef, ViewChild, Input, Output, EventEmitter, Inject, Optional, HostListener, NgZone, ChangeDetectorRef } from '@angular/core';
import { EditorFromTextArea } from 'codemirror';
import CodeMirror from 'codemirror';
import { css_beautify, js_beautify } from 'js-beautify';

import jsonlint from 'jsonlint-mod';
window["jsonlint"] = jsonlint;

import './../../../node_modules/codemirror/mode/javascript/javascript.js';
import './../../../node_modules/codemirror/mode/clike/clike.js';
import './../../../node_modules/codemirror/mode/css/css.js';

import './../../../node_modules/codemirror/addon/comment/comment.js';
import './../../../node_modules/codemirror/addon/comment/continuecomment.js';

import './../../../node_modules/codemirror/addon/dialog/dialog.js';

import './../../../node_modules/codemirror/addon/edit/closebrackets.js';
import './../../../node_modules/codemirror/addon/edit/matchbrackets.js';

import './../../../node_modules/codemirror/addon/fold/foldcode.js';
import './../../../node_modules/codemirror/addon/fold/brace-fold.js';
import './../../../node_modules/codemirror/addon/fold/comment-fold.js';
import './../../../node_modules/codemirror/addon/fold/indent-fold.js';//nothing happens
import './../../../node_modules/codemirror/addon/fold/foldgutter.js';

import './../../../node_modules/codemirror/addon/hint/show-hint.js';
import './../../../node_modules/codemirror/addon/hint/anyword-hint.js';
import './../../../node_modules/codemirror/addon/hint/javascript-hint.js';
import './../../../node_modules/codemirror/addon/hint/css-hint.js';

// import './../../../node_modules/codemirror/addon/lint/javascript-lint.js';
import './../../../node_modules/codemirror/addon/lint/lint.js';
import './../../../node_modules/codemirror/addon/lint/json-lint.js';
import './../../../node_modules/codemirror/addon/lint/css-lint.js';

import './../../../node_modules/codemirror/addon/dialog/dialog.js';
// import './../../../node_modules/codemirror/addon/tern/tern.js';

import { Subject } from '../../../node_modules/rxjs/Subject';
import { code } from '../../utilities/ecmascript';
import { tabDataInterface } from '../../utilities/interface.js';
import { MAT_SNACK_BAR_DATA } from '@angular/material';

@Component({
  selector: 'code-editor',
  templateUrl: 'code-editor.html'
})
export class CodeEditorComponent {

  @Input() inputDocument: Subject<CodeMirror.Doc> = new Subject();
  @Input() mode: tabDataInterface["mode"];
  @Output() showInterface: EventEmitter<string> = new EventEmitter();

  editor: EditorFromTextArea;
  @ViewChild('codemirror') codemirror: ElementRef;
  document: CodeMirror.Doc;

  constructor(@Optional() @Inject(MAT_SNACK_BAR_DATA) public data: { inputDoc: CodeMirror.Doc, mode: tabDataInterface["mode"] }, private change: ChangeDetectorRef) {
    let self = this;
    window.onresize = function () {
      self.editor.refresh();
    }
  }

  ngAfterContentChecked() {
    if (this.data != undefined) {
      this.document = this.data.inputDoc;
      this.mode = this.data.mode;
    }
  }

  ngAfterViewInit() {
    if (this.mode == "Javascript" || this.mode == "JSON") {
      this.createJSEditor();
    } else if (this.mode == "CSS") {
      this.createCSSEditor();
    } else if (this.mode == "Interface") {
      this.createInterfaceEditor();
      this.editorValue(this.document);
    }
    this.inputDocument.subscribe((val) => {
      this.document = val;
      this.editorValue(this.document);
      this.change.markForCheck();
    });
    document.getElementsByClassName('CodeMirror')[0].addEventListener('wheel', (event: WheelEvent) => {
      event.preventDefault();
      this.editor.scrollTo(null, this.editor.getScrollInfo().top + event.deltaY / 5);
    });
  }

  ngOnDestroy(): void {
    let fakeDoc1 = new CodeMirror.Doc('', 'javascript');
    this.editor.swapDoc(fakeDoc1);
    this.editor.toTextArea();
  }

  scrolled(event) {
    console.log(event);
  }

  createJSEditor() {
    this.editor = CodeMirror.fromTextArea(this.codemirror.nativeElement as HTMLTextAreaElement, {
      lineNumbers: true,
      // mode: this.component == undefined ? 'application/ld+json' : 'javascript',
      // mode: this.component == undefined ? 'application/json' : 'javascript',
      mode: this.mode == "Javascript" ? 'javascript' : 'application/ld+json',
      // mode: "text/x-objectivec",
      theme: 'material',
      foldGutter: true,
      gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter", "CodeMirror-lint-markers"],
      extraKeys: { "Ctrl-Space": "autocomplete" },
      lint: true,
      // scrollbarStyle: 'null',
    });
    this.editor.setOption('autoCloseBrackets', true);
    this.editor.setOption('matchBrackets', true);
    this.editor.setOption('continueComments', true);

    // let server = new CodeMirror["TernServer"]({ defs: [code] });
    // this.editor.setOption("extraKeys", {
    //   "Ctrl-Space": function (cm) { server.complete(cm); },
    //   "Ctrl-I": function (cm) { server.showType(cm); },
    //   "Ctrl-O": function (cm) { server.showDocs(cm); },
    //   "Alt-.": function (cm) { server.jumpToDef(cm); },
    //   "Alt-,": function (cm) { server.jumpBack(cm); },
    //   "Ctrl-Q": function (cm) { server.rename(cm); },
    //   "Ctrl-.": function (cm) { server.selectName(cm); }
    // });
    // this.editor.on("cursorActivity", (cm) => {
    //   server.updateArgHints(cm);
    // });
    // this.editor.on("change", (cm, obj) => {
    //   if (obj.text[0] == '.') {
    //     server.complete(cm);
    //   }
    // });

    // this.editor.on('blur', (cm) => {
    //   server.updateArgHints(cm);
    // });
  }

  createCSSEditor() {
    this.editor = CodeMirror.fromTextArea(this.codemirror.nativeElement as HTMLTextAreaElement, {
      lineNumbers: true,
      mode: "css",
      theme: 'material',
      foldGutter: true,
      gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter", "CodeMirror-lint-markers"],
      extraKeys: { "Ctrl-Space": "autocomplete" },
      lint: true
    });
    this.editor.setOption('autoCloseBrackets', true);
    this.editor.setOption('matchBrackets', true);
    this.editor.setOption('continueComments', true);
  }

  createInterfaceEditor() {
    this.editor = CodeMirror.fromTextArea(this.codemirror.nativeElement as HTMLTextAreaElement, {
      mode: "text/x-java",
      theme: 'material',
      readOnly: true,
      showCursorWhenSelecting: false,
      cursorBlinkRate: 100000,
    });
  }


  edit(event) {
    switch (event) {
      case 'Javascript':
        let codeJavascript = this.editor.getDoc();
        this.document.setValue(js_beautify(codeJavascript.getValue(), { indent_size: 2 }));
        break;
      case 'JSON':
        let codeJSON = this.editor.getDoc();
        this.document.setValue(js_beautify(codeJSON.getValue(), { indent_size: 2 }));
        break;
      case 'CSS':
        let codeCSS = this.editor.getDoc();
        this.document.setValue(css_beautify(codeCSS.getValue(), { indent_size: 2 }));
        break;
      case 'showInterface':
        this.showInterface.emit('');
        break;
      default:
        break;
    }
  }

  editorValue(doc: CodeMirror.Doc) {
    this.editor.swapDoc(doc);
  }

  showContextMenu(event: MouseEvent) {
    event.preventDefault();
  }
}



