import { Injectable, Inject } from '@angular/core';
import { globalConfigInterface, componentDataInterface, menuListInterface, tabDataInterface, consoleDataInterface, electronConfigInterface } from '../../utilities/interface';
import { firmata_config, globalSettings } from '../../utilities/code-samples';
import { Events } from 'ionic-angular';
import CodeMirror from 'codemirror';
import { css_beautify, js_beautify } from 'js-beautify';
import { addClassFromDump, addCSSFromDump, logger, addGlobalVariablesFromDump } from './global-service-dump';
import { DOCUMENT } from '@angular/platform-browser';
@Injectable()
export class GlobalServiceProvider {

  global_config = {
    json: new CodeMirror.Doc(js_beautify(JSON.stringify(firmata_config.js), { indent_size: 2 }), 'application/ld+json'),
    css: new CodeMirror.Doc(css_beautify(firmata_config.css, { indent_size: 2 }), 'css'),
    code: new CodeMirror.Doc(js_beautify(firmata_config.code, { indent_size: 2 }), 'javascript'),
    defaultSettings: new CodeMirror.Doc(js_beautify(JSON.stringify(globalSettings), { indent_size: 2 }), 'application/ld+json'),
    projectSettings: new CodeMirror.Doc(js_beautify('{  }'), 'application/ld+json'),
    userSettings: new CodeMirror.Doc(js_beautify('{  }'), 'application/ld+json'),
  }
  tabsArray: Array<tabDataInterface> = [];
  currentTab: tabDataInterface = {
    other: "globalConfigJSON",
    mode: "JSON"
  };
  designerComponents: Array<any> = [];
  comPortList: Array<any> = [];
  consoleData: consoleDataInterface = {
    top: -200,
    data: logger.data
  };
  electronConfig: electronConfigInterface = {
    parsedPath: null,
    quarkFile: this.getQuarkFile()
  }
  showLoading: boolean = false;

  constructor(private events: Events, @Inject(DOCUMENT) public document: Document) {
    this.appEvents();
    addGlobalVariablesFromDump(this);
    logger.log('Initializing Global Service');
  }

  appEvents() {
    this.events.subscribe('misc-components : global-service : add-component', (component: any) => {
      let flag = this.designerComponents.filter((val) => { return val.data.config.id == component.data.config.id });
      if (flag.length == 0) {
        this.designerComponents.push(component);
        let data: componentDataInterface = component.data;
        let json: globalConfigInterface["js"] = JSON.parse(this.global_config.json.getValue());
        json.rendered_views.push({ component: data.config.component, id: data.config.id, variable: data.config.variable });
        this.global_config.json.setValue(js_beautify(JSON.stringify(json), { indent_size: 2 }));
      }
    });

    this.events.subscribe('misc-components : global-service : add-tab', (tab: tabDataInterface) => {
      let data: componentDataInterface = tab.component.data;
      let newArray = this.tabsArray.find((val) => {
        if (val.component != undefined) {
          return val.component.data.config.id == data.config.id && val.mode == tab.mode;
        }
      });
      if (newArray == undefined) {
        this.tabsArray.push(tab);
        console.log(this.tabsArray);
      }
    });

    this.events.subscribe('activity-bar : global-service : add-tab', (tab: tabDataInterface) => {
      let newArray = this.tabsArray.find((val) => {
        if (val.other != undefined) {
          return val.other == tab.other && val.mode == tab.mode;
        }
      });
      if (newArray == undefined) {
        switch (tab.other) {
          case "globalConfigJSON":
            this.tabsArray.push({ mode: "JSON", other: "globalConfigJSON" });
            break;
          case "globalConfigCSS":
            this.tabsArray.push({ mode: "CSS", other: "globalConfigCSS" });
            break;
          case "globalConfigSetup":
            this.tabsArray.push({ mode: "Javascript", other: "globalConfigSetup" });
            break;
          case "globalConfigDefaultSettings":
            this.tabsArray.push({ mode: "JSON", other: "globalConfigDefaultSettings" });
            break;
          default:
            console.log("No tab");
            break;
        }
      }
    });

    this.events.subscribe('page-menu : global-service : add-class', (component: menuListInterface) => {
      addClassFromDump(component, this.global_config.json);
    });

    this.events.subscribe('page-designer : global-service : update-settings', () => {
      console.log(this.global_config.projectSettings.getValue());
    });
  }

  getQuarkFile(): object {
    let file = {
      globalConfig: {
        js: this.global_config.json.getValue(),
        css: this.global_config.css.getValue(),
        code: this.global_config.code.getValue(),
        styles: this.global_config.defaultSettings.getValue()
      },
      designerComponents: this.designerComponents.map((val) => {
        let data: componentDataInterface = val.data;
        let dataSub: any = {};
        dataSub.config = Object.assign({}, data.config);
        dataSub.localData = Object.assign({}, data.localData);
        dataSub.code = {};
        dataSub.code.js = data.code.js.getValue();
        dataSub.code.css = data.code.css.getValue();
        return JSON.stringify(dataSub);
      })
    }
    return file;
  }

  parseQuarkFile(file: string): Promise<void> {
    return new Promise((resolve, reject) => {
      let newDesignerComponents = [];
      try {
        let parse = JSON.parse(file);
        this.global_config.json = new CodeMirror.Doc(js_beautify(parse.globalConfig.js, { indent_size: 2 }), 'application/ld+json');
        this.global_config.css = new CodeMirror.Doc(css_beautify(parse.globalConfig.css, { indent_size: 2 }), 'css');
        this.global_config.code = new CodeMirror.Doc(js_beautify(parse.globalConfig.code, { indent_size: 2 }), 'javascript');
        this.global_config.defaultSettings = new CodeMirror.Doc(js_beautify(parse.global_config.styles, { indent_size: 2 }), 'application/ld+json');
        if (parse.designerComponents.length > 0) {
          parse.designerComponents.forEach((data: string, index) => {
            newDesignerComponents[index] = {};
            newDesignerComponents[index].data = {};
            newDesignerComponents[index].data = JSON.parse(data);
            newDesignerComponents[index].data.code = {};
            newDesignerComponents[index].data.code.js = new CodeMirror.Doc(js_beautify(JSON.parse(data).code.js, { indent_size: 2 }), 'javascript');
            newDesignerComponents[index].data.code.css = new CodeMirror.Doc(css_beautify(JSON.parse(data).code.css, { indent_size: 2 }), 'css');
          });
        }
        this.designerComponents = [];
        this.designerComponents = newDesignerComponents;
        this.events.publish('global-service : page-designer && page-code-mirror : recreate-designer-components');
        resolve();
      } catch (err) {
        reject(err);
      }
    });
  }

  addCSS(data: componentDataInterface) {
    addCSSFromDump(data);
  }
}      



