import { Injectable } from '@angular/core';
import * as socketIo from "socket.io-client";
import { Events } from 'ionic-angular';
import { componentDataInterface } from '../../utilities/interface';
import { GlobalServiceProvider } from '../global-service/global-service';

@Injectable()
export class SocketServiceProvider {

  socket: SocketIOClient.Socket;

  constructor(private events: Events, private gsp: GlobalServiceProvider) {
    this.appEvents();
  }

  connectToSocketServer(url: string) {
    console.log('Connecting');
    // this.socket = socketIo(`${url}:4300`);
    // this.socket.connect();
    // this.eventListeners();
  }

  eventListeners() {
    this.socket.on('connected', () => {
      console.log(`Connected at ${this.socket.id}`);
      this.events.publish('socket-service : connect-mobile : connected');
    });

    this.socket.on('com-port-list', (list) => {
      this.gsp.comPortList = list;
      console.log(list);
    });
    this.socket.on('com-port-error', (err) => {
      console.log(err);
    });

    this.socket.on('update-renderer', (closure) => {
      console.log(closure);
      this.events.publish('socket-service : misc-components : update-renderer', closure);
    });
  }

  appEvents() {
    this.events.subscribe('page-designer : socket-service : initialize', () => {
      let code = this.gsp.designerComponents.map((val) => {
        let data: componentDataInterface = val.data;
        return { functionName: data.config.function, code: data.code.js.getValue() };
      });
      console.log(code);
      this.socket.emit('initialize', this.gsp.global_config.json.getValue(), code);
    });

    this.events.subscribe('page-designer : socket-service : connect-to-board', () => {
      this.socket.emit('connect-to-board');
    });

    this.events.subscribe('misc-components : socket-service : firmata', (functionName, args) => {
      this.gsp.currentTab.mode == 'CSS'? null :
      this.socket.emit('firmata', functionName, args);
    });
  }
}



