import { Injectable, NgZone } from '@angular/core';
import { Platform, Events } from 'ionic-angular';
import { } from 'electron';
import { GlobalServiceProvider } from '../global-service/global-service';
import fs from "fs";
import Path from "path";
import { ChildProcess } from 'child_process';
import { MatSnackBar, MatSnackBarRef } from '@angular/material';
import { showAlertFromDump, newWindowHandleFromDump, createNewWindowFromDump, showSnackBarFromDump, createNewFileFromDump } from './electron-service-dump';
import { openRecentDocumentsInterface } from '../../utilities/interface';
import { runChildProcessFromDump } from './electron-service-assets-child-process';
import { showSaveDialogFromDump, saveFileFromDump, showOpenDialogFromDump } from './electron-service-assets-save&&open';

@Injectable()
export class ElectronServiceProvider {

  MenuItem: typeof Electron.MenuItem;
  Menu: typeof Electron.Menu;
  app: Electron.App;
  dialog: Electron.Dialog;
  BrowserWindow: typeof Electron.BrowserWindow;
  currentWebContents: Electron.WebContents;
  fs: typeof fs;
  Path: typeof Path;
  shell: Electron.Shell;
  childProcess: ChildProcess;
  fork: any;
  exec: any;
  mainProcess: any;
  openRecentDocumentsArray: Array<openRecentDocumentsInterface> = [];

  childProcessIsRunning: boolean = false;
  isDevMode?: boolean = true;
  snackBarRef: MatSnackBarRef<{}>;
  dependencies: Array<[{ name?: string; version?: string; }]> = [];

  constructor(public platform: Platform, public gsp: GlobalServiceProvider, public snackBar: MatSnackBar, public events: Events, private zone: NgZone) {
    this.platform.is('electron') ? this.injectElectron() : console.log('Cannot Inject Electron');
    this.events.subscribe('component-menu-bar : electron-service : run-setup', () => {
      this.killChildProcess();
      runChildProcessFromDump(this);
    });
    this.events.subscribe('component-menu-bar : electron-service : stop', () => {
      this.killChildProcess();
    });

    this.events.subscribe('misc-components : electron-service : child-process', (data: object) => {
      if (this.childProcessIsRunning) {
        this.childProcess.send(data);
      }
    });
    this.events.subscribe('component-new-file-dialog && component-my-dashboards : electron-service : close-snackbar', () => {
      this.snackBarRef.dismiss();
    });
    this.events.subscribe('component-new-file-dialog : electron-service : create-new-file', (data: Array<[{ name?: string; version?: string; }]>) => {
      createNewFileFromDump(this);
      this.dependencies = data;
    });
  }

  injectElectron() {
    const { Menu, MenuItem, app, dialog, BrowserWindow, getCurrentWebContents, shell, getGlobal } = require('electron').remote;
    const { fork, exec } = require('child_process');
    this.MenuItem = MenuItem;
    this.Menu = Menu;
    this.app = app;
    this.dialog = dialog;
    this.BrowserWindow = BrowserWindow;
    this.currentWebContents = getCurrentWebContents();
    this.shell = shell;
    this.fs = getGlobal('fileSystem');
    this.Path = getGlobal('path');
    this.mainProcess = getGlobal('process');
    this.fork = fork;
    this.exec = exec;
    console.log(process.argv, this.mainProcess.argv);
    if (`${this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW.length}` == '0') {
      if (this.mainProcess.argv[1] && !this.isDevMode) {
        this.mainProcess.env.PATH_TO_OPEN_IN_NEW_WINDOW = this.mainProcess.argv[1];
      }
    }
    newWindowHandleFromDump(this);
  }

  runNpmInstall() {
    if (this.dependencies.length > 0) {
      let npmString: string = '';
      if (this.dependencies[0].length > 0) {
        npmString = String().concat(npmString, 'npm install --save');
        this.dependencies[0].map((dep) => {
          npmString = String().concat(npmString, ` ${dep.name}${dep.version ? dep.version : ''} `);
        });
      }
      if (this.dependencies[1].length > 0) {
        npmString = String().concat(npmString, this.dependencies[0].length > 0 ? ' && npm install --save-dev ' : 'npm install --save-dev');
        this.dependencies[1].map((dep) => {
          npmString = String().concat(npmString, ` ${dep.name}${dep.version ? dep.version : ''} `);
        });
      }
      npmString = String().concat(npmString, '\r')
      this.events.publish('electron-service : component-console && component-terminal : run-npm-install', npmString);
    }
  }

  openInNewWindow() {
    this.dialog.showOpenDialog(this.BrowserWindow.fromWebContents(this.currentWebContents), {
      filters: [{
        extensions: ['qrk'],
        name: 'Quark'
      }],
      properties: ['openFile']
    }, (filePath) => {
      if (filePath) {
        createNewWindowFromDump(this, filePath[0]);
      }
    });
  }

  showNewFileDialog() {
    this.showAlert('info', 'Note', 'Do you want to create a package.json file and add external dependencies to your project?', ['Yes', 'No'])
      .then((val) => {
        this.zone.run(() => {
          if (val == 0) {
            showSnackBarFromDump(this, 'new-file-dialog');
          } else {
            showSaveDialogFromDump(this);
          }
        });
      })
      .catch((err) => {
        console.log(err);
      })
  }

  openExternalLink(url: string) {
    if (this.platform.is('electron')) {
      this.shell.openExternal(url);
    }
  }

  showAlert(type: 'info' | 'error' | 'question' | 'warning', title: string, message: string, buttons: Array<string>): Promise<number> {
    return new Promise((resolve, reject) => {
      if (this.platform.is('electron')) {
        showAlertFromDump(this, type, title, message, buttons, resolve);
      } else {
        reject();
      }
    });
  }


  showSaveDialog() {
    if (this.platform.is('electron')) {
      showSaveDialogFromDump(this);
    }
  }

  saveFile() {
    saveFileFromDump(this, this.gsp.electronConfig.parsedPath.dir);
  }

  showOpenDialog() {
    if (this.platform.is('electron')) {
      showOpenDialogFromDump(this);
    }
  }

  killChildProcess() {
    if (this.childProcessIsRunning) {
      this.childProcess.kill();
      // this.fs.unlink('./try.js', (err) => {
      //   console.log('Unlinking File', err);
      // });
    }
  }

  minimize() {
    this.platform.is('electron') ?
      this.BrowserWindow.getFocusedWindow().minimize() : null;
  }

  maximise() {
    if (this.platform.is('electron')) {
      if (this.BrowserWindow.getFocusedWindow().isMaximized()) {
        this.BrowserWindow.getFocusedWindow().unmaximize();
      } else {
        this.BrowserWindow.getFocusedWindow().maximize();
      }
    }
  }

  closeWindow() {
    if (this.platform.is('electron')) {
      this.BrowserWindow.fromWebContents(this.currentWebContents).close();
      this.app.setBadgeCount(this.BrowserWindow.getAllWindows().length);
    }
  }
}


