import { Component, NgZone } from '@angular/core';
import { Events } from 'ionic-angular';
import { Observable } from 'rxjs';
import { of } from 'rxjs/observable/of';

interface module {
  name?: string; version?: string;
}

@Component({
  selector: 'new-file-dialog',
  templateUrl: 'new-file-dialog.html'
})
export class NewFileDialogComponent {

  autocompleteDeps = ['serialport', 'johnny-five', 'cylonjs', 'firmatajs'];
  autocompleteDevDeps = ['serialport', 'johnny-five', 'cylonjs', 'firmatajs'];

  dependencies: Array<string> = [];
  devDependencies: Array<string> = [];
  editDependencies: Array<module> = [];
  editDevDependencies: Array<module> = [];

  npmInstall: boolean = false;

  constructor(private events: Events, private zone: NgZone) { }

  editTab() {
    this.zone.run(() => {
      this.editDependencies = this.findAndEdit(this.dependencies, this.editDependencies);
      this.editDevDependencies = this.findAndEdit(this.devDependencies, this.editDevDependencies);
    });
  }

  findAndEdit(copyFrom: Array<string>, copyTo: Array<module>): Array<module> {
    let temp = copyTo;
    copyTo = [];
    copyFrom.map((val) => {
      let find = temp.find((dep) => {
        return dep.name == val;
      });
      if (find) {
        copyTo.push(find);
      } else {
        copyTo.push({ name: val, version: null });
      }
    });
    return copyTo;
  }

  closeDialog() {
    this.events.publish('component-new-file-dialog && component-my-dashboards : electron-service : close-snackbar');
  }

  saveJSON() {
    let data = [];
    data[0] = this.editDependencies;
    data[1] = this.editDevDependencies;
    this.events.publish('component-new-file-dialog : electron-service : create-new-file', data);
    this.events.publish('component-new-file-dialog && component-my-dashboards : electron-service : close-snackbar');
  }

  transform(value: string): Observable<object> {
    const item = { display: `@${value}`, value: `@${value}` };
    return of(item);
  }
}



