import { menuListInterface, componentDataInterface } from "../../utilities/interface";
import { GlobalServiceProvider } from "./global-service";
import { settingsInterface } from "../../utilities/settings-interface";

export function addGlobalVariablesFromDump(_this: GlobalServiceProvider) {
    let defaults: settingsInterface = JSON.parse(_this.global_config.defaultSettings.getValue());
    for (const color of Object.keys(defaults.styles.theme)) {
        for (const type of Object.keys(defaults.styles.theme[color])) {
            _this.document.documentElement.style.setProperty(`--${color}-color-${type}`, defaults.styles.theme[color][type]);
            // console.log(`--${color}-color-${type}`, defaults.styles.theme[color][type]);
        }
    }
}

export function addCSSFromDump(data: componentDataInterface) {
    try {
        let prevStyle = document.getElementById(`style-${data.config.id}`);
        prevStyle.parentNode.removeChild(prevStyle);
    } catch (err) {
        null;
    }
    let edit = String().concat(`}`, data.code.css.getValue());
    edit = edit.replace(/}/g, `}.${data.config.css_class} `);
    edit = edit.slice(1, edit.length - data.config.css_class.length - 2);
    let style = document.createElement('style');
    style.setAttribute('id', `style-${data.config.id}`);
    style.innerHTML = edit;
    document.body.appendChild(style);
}

export const logger = {
    data: [],
    log: (message: string, indent?: number, fullColor?: boolean) => {
        logger.data.push({ message: message, indent: indent, type: 'log', fullColor: fullColor, timestamp: indent ? 1 : Date.now() })
    },
    warn: (message: string, indent?: number, fullColor?: boolean) => {
        logger.data.push({ message: message, indent: indent, type: 'warn', fullColor: fullColor, timestamp: indent ? 1 : Date.now() })
    },
    error: (message: string, indent?: number, fullColor?: boolean) => {
        logger.data.push({ message: message, indent: indent, type: 'error', fullColor: fullColor, timestamp: indent ? 1 : Date.now() })
    },
    right: (message: string, indent?: number, fullColor?: boolean) => {
        logger.data.push({ message: message, indent: indent, type: 'right', fullColor: fullColor, timestamp: indent ? 1 : Date.now() })
    },
    indent: (message: string, indent?: number, fullColor?: boolean) => {
        logger.data.push({ message: message, indent: indent, type: 'indent', fullColor: fullColor, timestamp: indent ? 1 : Date.now() })
    }
}


