import { Component, ViewEncapsulation } from '@angular/core';
import { MatTooltipDefaultOptions, MatSnackBarRef } from '@angular/material';
import { Events, NavController } from 'ionic-angular';
import { ElectronServiceProvider } from '../../providers/electron-service/electron-service';
import { GlobalServiceProvider } from '../../providers/global-service/global-service';
import { AuthenticationServiceProvider } from '../../providers/authentication-service/authentication-service';
import { createNewWindowFromDump, showSnackBarFromDump } from '../../providers/electron-service/electron-service-dump';

export const myCustomTooltipDefaults: MatTooltipDefaultOptions = {
  showDelay: 0,
  hideDelay: 5000,
  touchendHideDelay: 1000,
};

@Component({
  selector: 'activity-bar',
  templateUrl: 'activity-bar.html',
  // providers: [
  //   { provide: MAT_TOOLTIP_DEFAULT_OPTIONS, useValue: myCustomTooltipDefaults }
  // ],
  encapsulation: ViewEncapsulation.None
})
export class ActivityBarComponent {

  width: number;
  snackBarRef: MatSnackBarRef<{}>;

  constructor(private events: Events, private navCtrl: NavController, public esp: ElectronServiceProvider, private gsp: GlobalServiceProvider, public asp: AuthenticationServiceProvider) { }

  menuFile(event: string) {
    switch (event) {
      case 'new':
        this.esp.showNewFileDialog();
        break;
      case 'save':
        this.gsp.electronConfig.parsedPath == null ?
          this.esp.showSaveDialog() : this.esp.saveFile();
        break;
      case 'save-as':
        this.esp.showSaveDialog()
        break;
      case 'open':
        this.esp.showOpenDialog();
        break;
      case 'open-in-new-window':
        this.esp.openInNewWindow();
        break;
      default:
        break;
    }
  }

  openRecentDocument(filePath: string) {
    createNewWindowFromDump(this.esp, filePath);
  }

  menuCloud(event: string) {
    switch (event) {
      case 'upload':
        this.asp.uploadDashboard();
        break;
      case 'your-dashboards':
        showSnackBarFromDump(this.esp, 'my-dashboards');
        break;
      case 'usage':
        break;
      default:
        break;
    }
  }

  activity(activity: string) {
    switch (activity) {
      case 'connect':
        this.events.publish('page-designer : socket-service : connect-to-board');
        break;
      case 'initialize':
        this.events.publish('page-designer : socket-service : initialize');
        break;
      case 'globalConfigJSON':
        this.events.publish('activity-bar : global-service : add-tab', { other: "globalConfigJSON", mode: "JSON" });
        break;
      case 'globalConfigCSS':
        this.events.publish('activity-bar : global-service : add-tab', { other: "globalConfigCSS", mode: "CSS" });
        break;
      case 'globalConfigSetup':
        this.events.publish('activity-bar : global-service : add-tab', { other: "globalConfigSetup", mode: "Javascript" });
        break;
      case 'globalConfigDefaultSettings': 
        this.events.publish('activity-bar : global-service : add-tab', { other: "globalConfigDefaultSettings", mode: "JSON" });
        break;
      case 'goToDesignerPage':
        this.events.publish('component-activity-bar : page-designer : show-designer');
        break;
      default:
        break;
    }
  }
}



