import { globalConfigInterface, componentDataInterface } from "../../utilities/interface";
import { showSaveDialogFromDump } from "./electron-service-assets-save&&open";

export function runChildProcessFromDump(_this) {
    if (!_this.gsp.electronConfig.fileName) {
        _this.showAlert('info', 'File not saved', 'You must save the file first', ['Save', 'Cancel'])
            .then((response: number) => {
                console.log(response);
                if (response == 0) {
                    showSaveDialogFromDump(_this);
                }
            })
            .catch((err) => {
                console.log('Error runnung child process');
            });
        return;
    }

    let self = _this;
    let file: string = '';
    let imports: globalConfigInterface['js']['imports'] = JSON.parse(self.gsp.global_config.js.getValue()).imports;
    let code: globalConfigInterface['code'] = self.gsp.global_config.code.getValue();
    let designerComponents: Array<any> = _this.gsp.designerComponents;
    for (let i = 0; i < imports.length; i++) {
        file = String().concat(file, `let ${imports[i].name} = require('${imports[i].module}');`);
    }
    file = String().concat(file, 'let functionsObjectForInternalUse={};');
    file = String().concat(file, 'let closureObjectForInternalUse={};');
    file = String().concat(file, 'let aliasForProcess=process;');
    file = String().concat(file, `let frameRateForInternalUse=${JSON.parse(self.gsp.global_config.js.getValue()).frameRate};`);
    designerComponents.map((val) => {
        let data: componentDataInterface = val.data;
        file = String().concat(file, `let ${data.config.variable} = JSON.parse('${JSON.stringify(data.localData)}');`);
        file = String().concat(file, `functionsObjectForInternalUse['${data.config.function}'] = function(args){${data.code.js.getValue()}}`);
        file = String().concat(file, `closureObjectForInternalUse['${data.config.variable}'] = ${data.config.variable};`);
    });
    file = String().concat(file, `(function(){${code};`);
    file = String().concat(file,
        `
      aliasForProcess.send('process-started');
      setInterval(()=>{
        aliasForProcess.send(closureObjectForInternalUse);
      },(1000/frameRateForInternalUse));
      
      aliasForProcess.on('message', (data)=> {
        try{
            functionsObjectForInternalUse[data.functionName](data.args);
            //process.send(data);
        }catch(err){
          aliasForProcess.send({errMsg : err});
        }
    });`);
    file = String().concat(file, '})();');

    _this.fs.writeFile(`${_this.gsp.electronConfig.folderPath}/try.js`, js_beautify(file), (err: NodeJS.ErrnoException) => {
        console.log(err);
        if (err == null) {
            startChildProcess();
        }
    });

    function startChildProcess() {
        self.childProcess = self.fork(`${self.gsp.electronConfig.folderPath}/try.js`);
        self.childProcess.on('error', (err) => {
            console.log(err);
        });
        self.childProcess.on('close', (close) => {
            console.log("Close", close);
            self.childProcessIsRunning = false;
        });
        self.childProcess.on('message', (msg) => {
            if (msg == "process-started") {
                self.childProcessIsRunning = true;
            } else if (msg.errMsg != undefined) {
                console.log('Error in process', msg);
            } else {
                self.events.publish('electron-service-dump : misc-components : update-renderer', msg);
            }
        });
        self.childProcess.on('exit', (code: number, signal: string) => {
            console.log(`child process exited with code ${code} and signal ${signal}`);
            self.childProcessIsRunning = false;
        });
    }
}


